import FavCard from "./FavCard";
import "./favorite.scss";
import smartphone from "../../images/gadgets/smatrphone.png";
import computer from "../../images/gadgets/computer.png";
import monitor from "../../images/gadgets/monitor.png";
import accsessuar from "../../images/gadgets/accsessuar.png";

export default function Favorite() {
  return (
    <div className="favorite">
        <h2 > </h2>
      <div className="favorite__wrapper">
        <FavCard img={smartphone} title="" />
        <FavCard img={monitor} title="" />
        <FavCard img={computer} title="" />
        <FavCard img={accsessuar} title="" />
      </div>
    </div>
  );
}
